.no-scroll {
  overflow: hidden;
}

.section_navbar {
  z-index: 1000;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.nav-links-wrap {
  align-items: center;
  display: flex;
}

.burger-wrap {
  cursor: pointer;
  display: none;
}

@media screen and (width <= 991px) {
  .burger-wrap {
    display: block;
  }

  .nav-links-wrap {
    background: #000000e6;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: none;
    position: fixed;
    inset: 0;
  }
}
